import { USERLOGIN_FAIL, USERLOGIN_REQUEST, USERLOGIN_SUCCESS, USERLOGOUT, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS } from "../Constants/UserConstants";
import axios from "axios"
import URL from "../Url"

export const login = (email,password)=>async(dispatch)=>{
    try {
        dispatch({type:USERLOGIN_REQUEST});

        const config={
           headers:{
            "Content-Type":"application/json"
           } 
        }
        const {data} = await axios.post(`${URL}/api/users/login`,{email,password},config);
        dispatch({type:USERLOGIN_SUCCESS, payload:data});
        localStorage.setItem("userInfo",JSON.stringify(data))
    } catch (error) {
        dispatch({
            type:USERLOGIN_FAIL,
            payload:
            error.response && error.response.data.message ? error.response.data.message:
            error.message,
        });
    }
};

export const register = (name,email,password)=>async(dispatch)=>{
    try {
        dispatch({type:USER_REGISTER_REQUEST});

        const config={
           headers:{
            "Content-Type":"application/json"
           } 
        }
        const {data} = await axios.post(`${URL}/api/users`,{name,email,password},config);
        dispatch({type:USER_REGISTER_SUCCESS, payload:data});
        dispatch({type:USERLOGIN_SUCCESS, payload:data});
        localStorage.setItem("userInfo",JSON.stringify(data))
    } catch (error) {
        dispatch({
            type:USER_REGISTER_FAIL,
            payload:
            error.response && error.response.data.message ? error.response.data.message:
            error.message,
        });
    }
};


export const logout = () =>async(dispatch)=>{
    localStorage.removeItem("userInfo")
    dispatch({type:USERLOGOUT})
    document.location.href="/login"
}