import { USERLOGIN_FAIL, USERLOGIN_REQUEST, USERLOGIN_SUCCESS, USERLOGOUT, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS } from "../Constants/UserConstants";

export const userLoginReducer = (state={},action)=>{
    switch(action.type){
        case USERLOGIN_REQUEST:
            return {loading:true};
        case USERLOGIN_SUCCESS:
            return {loading:false, userInfo:action.payload};
        case USERLOGIN_FAIL:
            return {loading:false, error:action.payload};
        case USERLOGOUT:
            return {};
        default:
            return state;
    }
}

export const userRegisterReducer = (state={},action)=>{
    switch(action.type){
        case USER_REGISTER_REQUEST:
            return {loading:true};
        case USER_REGISTER_SUCCESS:
            return {loading:false, userInfo:action.payload};
        case USER_REGISTER_FAIL:
            return {loading:false, error:action.payload};
        default:
            return state;
    }
}